import React from 'react';
import MiProducto from '../MyProduct'
import { Product, ProductList, Span, Title, TitleContainer } from './styled'
const Component = (props) => {
    const { productsData, change, setChange, isMyProduct } = props;
    const dataVerify = productsData.length > 0;

    return (
        <ProductList>
            {dataVerify ? null : <TitleContainer><Title>No has publicado ningun producto <Span onClick={() => window.location.href = "/agregar-producto"}>hazlo ahora</Span></Title> </TitleContainer>}

            {productsData.map(producto =>
                <Product key={`product-${producto.idProduct}`}>
                    <MiProducto
                        dataProduct={producto}
                        isMyProduct={isMyProduct}
                        setChange={setChange}
                        change={change}
                    />
                </Product>
            )}
        </ProductList>
    );
}

export default Component;
export { Component as ProductList }